.products__container {
  background-color: $background-white;
  padding-top: 121px;
  padding-bottom: 98px;
  @media screen and (min-width: 768px) {
    padding-top: 122px;
    padding-bottom: 112px;
  }
  @media screen and (min-width: 1280px) {
    padding-top: 127px;
    padding-bottom: 119px;
  }
}

.products__title-wrapper {
  margin-bottom: 144px;

  @media screen and (min-width: 768px) {
    margin-bottom: 150px;
  }
  @media screen and (min-width: 1280px) {
    margin-bottom: 259px;
  }
}
.products__double-title {
  font-family: Titan One, sans-serif;
  text-align: center;
  text-transform: uppercase;
  font-size: 18px;
  line-height: 1.166;

  margin-bottom: 15px;

  color: $text-pink;

  @media screen and (min-width: 768px) {
    font-size: 26px;
    line-height: 1.154;
  }

  @media screen and (min-width: 1280px) {
    font-size: 30px;
    line-height: 1.133;

    letter-spacing: 0.04em;
  }
}

.products__title {
  font-family: Titan One, sans-serif;
  text-transform: uppercase;

  font-size: 34px;
  line-height: 1.147;
  letter-spacing: 0.04em;
  text-align: center;

  color: $text-red;

  @media screen and (min-width: 768px) {
    font-size: 48px;
    line-height: 1.145;
  }

  @media screen and (min-width: 1280px) {
    font-size: 58px;
    line-height: 1.138;
  }
}

.products__list {
  @media screen and (min-width: 768px) {
    display: flex;
  }
}

.products__card {
  margin-left: auto;
  margin-right: auto;
  height: auto;
  border-radius: 24px;

  text-align: center;

  @media screen and (max-width: 767px) {
    max-width: 280px;

    &:not(:last-child) {
      margin-bottom: 130px;
    }
  }
  @media screen and (min-width: 768px) {
    max-width: 220px;
    &:not(:first-child) {
      margin-left: 20px;
    }
  }
  @media screen and (min-width: 1280px) {
    max-width: 335px;
    &:not(:first-child) {
      margin-left: 24px;
    }
  }
  &.products__card--one {
    background-color: $background-one-color;
  }
  &.products__card--two {
    background-color: $background-two-color;
  }
  &.products__card--three {
    background-color: $background-three-color;
  }
  &::before {
    display: inline-block;
    content: '';
    width: 100%;
    height: 100%;
    max-width: 249px;
    height: 252px;

    background-repeat: no-repeat;
    background-position-x: 50%;
    background-position-y: 98%;

    margin-top: -113px;
    margin-bottom: 22px;

    @media screen and (min-width: 768px) {
      display: flex;
      content: '';
      max-width: 220px;
      height: 226px;
      margin-top: -104px;
      margin-bottom: 30px;

      background-position-y: 90%;
      justify-content: center;
    }
    @media screen and (min-width: 1280px) {
      content: '';
      max-width: 335px;
      height: 335px;
      margin-top: -210px;
      margin-bottom: 38px;
      background-position-y: 90%;
    }
  }
}
