.products__card-link {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  padding-left: 16px;
  padding-right: 16px;
}

.products__card-subtitle {
  font-family: Titan One, sans-serif;
  font-weight: 400;
  font-size: 20px;
  line-height: 1.15;
  text-align: center;
  letter-spacing: 0.04em;
  text-transform: uppercase;

  color: $background-white;
  @media screen and (max-width: 1279px) {
    margin-bottom: 30px;
  }
  @media screen and (min-width: 1280px) {
    font-size: 30px;
    line-height: 1.13;

    margin-bottom: 32px;
  }
}

.products__card-subtitle::after {
  content: '';
  display: inline-block;
  width: 100%;
  height: 6px;
  background-image: url(../images/point@1x.png);
  background-repeat: no-repeat;
  background-position: center;
  margin-top: 30px;
  @media screen and (min-width: 1280px) {
    margin-top: 32px;
  }
}

.products__card-text {
  font-weight: 700;
  font-size: 14px;
  line-height: 1.52;
  text-align: center;
  letter-spacing: 0.04em;
  margin: 0 auto;
  margin-bottom: 30px;

  color: $background-white;
 
  @media screen and (min-width: 768px) {
    line-height: 1.76;
    margin-bottom: 40px;
  }
  @media screen and (min-width: 1280px) {
    font-size: 16px;
    margin-bottom: 62px;
  }
}
/*style button open/closed*/

.products__card-button::after, .products__button-closed::after {
  content: '';
  display: block;
  width: 40px;
  height: 40px;
  border-radius: 50%;
  background-image: url(../images/arrow-right@1x.png);
  background-repeat: no-repeat;
  background-position: center;

  transition: transform 250ms linear;
}
.products__card-button, .products__button-closed {
  padding-bottom: 50px;
  border-radius: 50%;

  transition: transform $transition-options;
 

  @media screen and (max-width: 767px) {
    padding-bottom: 41px;
  }
  @media screen and (min-width: 768px) {
    padding-bottom: 40px;
  }
  @media screen and (min-width: 1280px) {
    padding-bottom: 50px;
  }
}
.products__card-button:hover::after,  .products__card-button:focus::after{
  transform: rotate(-90deg);
}
.products__button-closed:hover::after, .products__button-closed:focus::after {
  transform: rotate(90deg);
}

