.modal-one {
  background-color: $background-one-color;
}
.modal-two {
  background-color: $background-two-color;
}
.modal-three {
  background-color: $background-three-color;
}

/*new modal*/
.overlay {
  position: fixed;
  top: 25%;
  left: 0;
  width: 100%;
  height: 75%;
  background: rgba(255, 184, 202, 0.2);
  border-radius: 24px;

  /*z-index: 1;*/
  opacity: 0;
  visibility: hidden;
  transition: opacity 250ms linear;
/*не дає прокручуватися контенту під модальним вікном*/
  overflow-y: auto;
}

/* Стилі для модального вікна*/

.modalpro {
  position: absolute;
  max-width: 450px;
  width: 100%;
  padding: 0 16px;
  top: 50%;
  left: 0;
  border-radius: 24px;

  box-shadow: 0px 1px 3px rgba(0, 0, 0, 0.12), 0px 1px 1px rgba(0, 0, 0, 0.14), 0px 2px 1px rgba(0, 0, 0, 0.2);
  opacity: 0;

  transform: translateY(-50%);
  transition: transform 250ms linear;

   /* @media screen and (min-width: 768px) {
      transform: translate(-50%, -50%);
    }

    @media screen and (min-width: 1200px) {
      min-height: 424px; 
    }*/
}
/*.products__card-button:hover  {
  transform: translateY(-100%);
}*/
/* Стилі для активних класів підложки та вікна*/
.modalpro.active,
.overlay.active{
   opacity: 1;
   visibility: visible;
}